    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    box-sizing: border-box;
}

body{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    margin: 0;
    height: 100vh;
}

#first-container{
    margin-top: 0.5rem;
    height: 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#first-container > h1{
    color: white;
    font-size: 3rem;
    font-style: italic;
    margin: 0;
}

#first-container > h3 {
    color: white;
}

#big-container{
    background-color: rgba(145, 151, 184, 0.849);
    padding: 1.5rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-height: 90%;
    width: 70vw;
    position: relative;
}

#return{
    display: none;
}

#return.active{
    display: inline;
    position: absolute;
    right: 87%;
    top: 5rem;
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: bold;
    color: rgb(48, 74, 223);
}

#return:hover{
    cursor: pointer;
    color: rgb(62, 77, 163);
    
}

#t-toggle{
    position: absolute;
    top: 2rem;
    left: 70%;
    font-size: 0.8rem;
}

input{
    outline-width: 0;
    padding: 0.5rem;
    border-style: none;
}

button{
    padding: 0.5rem;
    background-color: rgb(85, 105, 221);
    border-style: none;
    color: white;
    border-radius: 0.25rem;
}

button:hover{
    background-color: rgb(48, 74, 223);
    cursor: pointer;
}

#loader-div{
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 2rem;
}

.loader {
  display: none;
  margin-top: 1rem;
  border: 0.5rem solid #f3f3f3; 
  border-top: 0.5rem solid #3498db;
  border-radius: 50%;
  width: 0.5rem;
  height: 0.5rem;
  animation: spin 1.5s linear infinite;
}

.loader.active{
    display: block;
}

.icon{
    height: 5rem;
    width: 5rem;
}

#grid-weather{
    padding: 1rem;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(3, 1fr);
    
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.cell{
    position: relative;
    bottom: 0;
    height: 11rem;
    width: 10rem;
    display: grid;
    grid-template-rows: 1fr 8fr 1fr;
    background-color: white;
    padding: 0.5rem 1.5rem;
    text-align: center;
    margin: 0.5rem;
    border-radius: 0.4rem;
    transition: bottom ease-out 0.2s, box-shadow ease-out 0.2s;
    box-shadow: none
}

.cell:hover{
    cursor: pointer;
    position: relative;
    bottom: 0.3rem;
    box-shadow: 0.4rem 0.4rem 0.4rem rgb(107, 112, 136);
    transition: bottom ease-out 0.2s, box-shadow ease-out 0.2s;
}

.bodyCell{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    font-size: 2rem;
    
}

.footCell{
    font-size: 0.8rem;
}



#div1{
    display: flex;
    justify-content: space-around;
    width: 100%;
}

#div2{
    max-height: 100%;
    overflow: scroll;
}

#div1 > img {
    height: 8rem;
    width: 8rem;
}

#div1temp{
    font-size: 4rem;
}

#div1data{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#details.active{
    display: flex;
    flex-direction: column;
    width: 70vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-height: 90%;
}

.smallIcon{
    width: 1.5rem;
}

#tabla{
    overflow-y: scroll;
    width: 80%;
    display: flex;
    justify-content: center;
    margin-top: 3rem;
}

table{
    width: 100%;
    background-color: white;
    border: 1px grey solid;
    border-collapse: collapse;
}

td, th{
    text-align: center;
    padding: 0.5rem;
    border: 1px rgb(154, 175, 180) solid;
}

tr:nth-child(2n+3){
    background-color: rgb(190, 204, 207);
}

tr:first-child{
    background-color: rgb(154, 175, 180);
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,qGAAqG;IACrG,sBAAsB;AAC1B;;AAEA;IACI,yDAAyD;IACzD,sBAAsB;IACtB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;IACT,SAAS;IACT,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,kBAAkB;IAClB,SAAS;AACb;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,4CAA4C;IAC5C,eAAe;IACf,mBAAmB;IACnB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;IACf,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,UAAU;IACV,SAAS;IACT,qBAAqB;IACrB,iBAAiB;IACjB,iBAAiB;IACjB,uBAAuB;AAC3B;;AAEA;IACI,eAAe;IACf,uBAAuB;;AAE3B;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;IAChB,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,mCAAmC;IACnC,kBAAkB;IAClB,YAAY;IACZ,sBAAsB;AAC1B;;AAEA;IACI,kCAAkC;IAClC,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,aAAa;AACjB;;AAEA;EACE,aAAa;EACb,gBAAgB;EAChB,4BAA4B;EAC5B,gCAAgC;EAChC,kBAAkB;EAClB,aAAa;EACb,cAAc;EACd,oCAAoC;AACtC;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,aAAa;IACb,aAAa;IACb,qCAAqC;IACrC,kCAAkC;;AAEtC;;AAEA;EACE,KAAK,uBAAuB,EAAE;EAC9B,OAAO,yBAAyB,EAAE;AACpC;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,aAAa;IACb,YAAY;IACZ,aAAa;IACb,+BAA+B;IAC/B,uBAAuB;IACvB,sBAAsB;IACtB,kBAAkB;IAClB,cAAc;IACd,qBAAqB;IACrB,0DAA0D;IAC1D;AACJ;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,cAAc;IACd,mDAAmD;IACnD,0DAA0D;AAC9D;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,6BAA6B;IAC7B,eAAe;;AAEnB;;AAEA;IACI,iBAAiB;AACrB;;;;AAIA;IACI,aAAa;IACb,6BAA6B;IAC7B,WAAW;AACf;;AAEA;IACI,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,eAAe;AACnB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,aAAa;IACb,uBAAuB;IACvB,gBAAgB;AACpB;;AAEA;IACI,WAAW;IACX,uBAAuB;IACvB,sBAAsB;IACtB,yBAAyB;AAC7B;;AAEA;IACI,kBAAkB;IAClB,eA